import React, { useState, useEffect } from 'react'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars, faTimes } from '@fortawesome/free-solid-svg-icons'

export const useScroll = () => {

    // Set a single object `{ x: ..., y: ..., direction: ... }` once on init
    const [scroll, setScroll] = useState({
        // x: document.body.getBoundingClientRect().left,
        // y: document.body.getBoundingClientRect().top,
        x: window.scrollX,
        y: window.scrollY,
        direction: ''
    })

    const listener = e => {
        // `prev` provides us the previous state: https://reactjs.org/docs/hooks-reference.html#functional-updates
        setScroll(prev => ({
            x: window.scrollX,
            y: window.scrollY,
            // Here we’re comparing the previous state to the current state to get the scroll direction
            direction: prev.y > window.scrollY ? 'up' : 'down'
        }))
    }

    useEffect(() => {
        window.addEventListener('scroll', listener)
        // cleanup function occurs on unmount
        return () => window.removeEventListener('scroll', listener)
        // Run `useEffect` only once on mount, so add `, []` after the closing curly brace }
    }, [])

    return scroll
}

// function intialiseToolbar() {

//     let y = document.documentElement.scrollTop || document.body.scrollTop
//     console.log({ y })
//     if (y > 0) return true
//     return false
// }

const Navbar = (props) => {
    const [active, setActive] = useState(false)
    const [mobileActive, setMobileActive] = useState(false)

    const scroll = useScroll();

    // useEffect(() => {
    //     window.scrollTo(0, 0);
    // }, [])
    useEffect(() => {
        // console.log(scroll)

        if (scroll.y === 0) {
            setActive(false)
        } else {
            setActive(true)
        }
    }, [scroll])

    return (
        <>

            <div className={(() => {
                return `menu ${(active) ? "menu--active" : ""}`
            })()}>
                <div className="container">
                    <div className="row">
                        <div className="menu__wrapper d-none d-lg-block col-md-12">
                            <nav className="">
                                <ul>
                                    <li><a href="#hello">Hey</a></li>
                                    <li><a href="#resume">Resume</a></li>
                                    <li><a href="#portfolio">Projects</a></li>
                                    {/* <li><a href="#blog">blog</a></li> */}
                                    <li><a href="#contact">Contact</a></li>
                                </ul>
                            </nav>
                        </div>
                        <div className="menu__wrapper col-md-12 d-lg-none">
                            <button type="button" className="menu__mobile-button" onClick={() => { setMobileActive(true) }}>
                                <span><FontAwesomeIcon icon={faBars} /></span>
                            </button>
                        </div>
                    </div>
                </div>
            </div>



            <div className={(() => {
                return `mobile-menu d-lg-none ${(active) ? "mobile-menu--active" : ""} ${(mobileActive) ? "active" : ""}`
            })()}>
                <div className="container">
                    <div className="mobile-menu__close" onClick={() => { setMobileActive(false) }}>
                        <span><FontAwesomeIcon icon={faTimes} /></span>
                    </div>
                    <nav className="mobile-menu__wrapper">
                        <ul>
                            <li><a onClick={() => { setMobileActive(false) }} href="#hello">Hey</a></li>
                            <li><a onClick={() => { setMobileActive(false) }} href="#resume">Resume</a></li>
                            <li><a onClick={() => { setMobileActive(false) }} href="#portfolio">Projects</a></li>
                            {/* <!-- <li><a href="#testimonials">testimonials</a></li> --> */}
                            {/* <!-- <li><a href="#blog">blog</a></li> --> */}
                            <li><a onClick={() => { setMobileActive(false) }} href="#contact">Contact</a></li>
                        </ul>
                    </nav>
                </div>
            </div>

        </>
    );
}

export default Navbar;