import React, { useState, useEffect } from 'react'
import { projects } from '../data/projects_data'
import { Link } from 'react-router-dom'

const ProjectCard = (f_key, banner, name, description, tech_stack, links) => {
    let keys = f_key
    return {
        ui: (<>
            <div className="row project-card" data-portfolio-tag="web-sites" key={f_key}>
                <div className="col-md-6 col-lg-5 project-card__img">
                    <img className="" src={banner} alt="project-img" />
                </div>
                <div className="col-md-6 col-lg-7 project-card__info">
                    <h3 className="project-card__title">{name}</h3>
                    <p className="project-card__description">
                        {description}
                    </p>
                    <p className="project-card__stack">Used stack:</p>
                    <ul className="tags">
                        {tech_stack.map((j, _k) => <li key={_k}>{j}</li>)}
                    </ul>
                    {links.map((j, _k) => <a href={j.link} style={{ marginLeft: "12px" }} key={_k} className="project-card__link">{j.name}</a>)}
                </div>
            </div>
        </>),
        key: keys
    }
}

const Projects = (props) => {
    const [tags, setTags] = useState(["ALL"])
    const [activeTag, setActiveTag] = useState("ALL")
    const [taggedProjects, setTaggedProjects] = useState({})
    const [c_projects, setC_Projects] = useState([])
    useEffect(() => {
        let t = []
        let projects_t = {}
        t.push("ALL")

        let curated_projects = []

        if (props.pinnedOnly) {
            for (let i = 0; i < projects.length; i++) {
                if (projects[i].pinned) {
                    curated_projects.push(projects[i])
                }
            }
        } else {
            curated_projects = projects
        }

        setC_Projects(curated_projects)

        for (let i = 0; i < curated_projects.length; i++) {
            let p = curated_projects[i]
            curated_projects[i].key = "project." + i
            if (t.indexOf(p.tag.replace(/_/g, " ").toUpperCase()) === -1) {
                t.push(p.tag.replace(/_/g, " ").toUpperCase())
            }

            if (projects_t[p.tag.replace(/_/g, " ").toUpperCase()]) {
                projects_t[p.tag.replace(/_/g, " ").toUpperCase()].push(
                    { ...p }
                )
            } else {
                projects_t[p.tag.replace(/_/g, " ").toUpperCase()] = []
                projects_t[p.tag.replace(/_/g, " ").toUpperCase()].push(
                    { ...p }
                )
            }
        }

        setTags(t)
        setTaggedProjects(projects_t)
        // console.log({ curated_projects, projects })
        // eslint-disable-next-line
    }, [])
    return (
        <>
            <section id="portfolio" className="container section">
                <div className="row">
                    <div className="col-md-12">
                        <h2 id="portfolio_header" className="section__title">My projects_</h2>
                    </div>
                </div>


                <div className="row portfolio-menu">
                    <div className="col-md-12">
                        <nav>
                            <ul>
                                {(() => {
                                    let keys = 0;
                                    return tags.map(i => {
                                        return <li key={keys++}>
                                            <a
                                                className={(activeTag === i) ? "portfolio-menu__link--active" : ""}
                                                href="#&nbsp;"
                                                onClick={() => { setActiveTag(i) }}
                                                data-portfolio-target-tag={i}>{i}</a>
                                        </li>
                                    })
                                })()}

                            </ul>
                        </nav>
                    </div>
                </div>

                <div className="portfolio-cards">

                    {(() => {
                        if (activeTag === "ALL") {
                            // let _k = 0
                            return c_projects.map((i, k) => {
                                let res = ProjectCard(i.key, i.banner, i.name, i.description, i.tech_stack, i.links)
                                // _k += res.key++
                                return <div key={k}>{res.ui}</div>
                            })
                        } else {
                            // let _k = 0
                            return taggedProjects[activeTag].map((i, k) => {
                                let res = ProjectCard(i.key, i.banner, i.name, i.description, i.tech_stack, i.links)
                                // _k += res.key++
                                return <div key={k}>{res.ui}</div>
                            })
                        }
                    })()}
                </div>
                {(() => {
                    if (props.pinnedOnly) {
                        return <div className="w-100">
                            <Link to="/projects" style={{
                                color: "unset"
                            }}>See all projects →</Link>
                        </div>
                    } else {
                        return <div className="w-100">
                            <Link to="/" style={{
                                color: "unset"
                            }}>← Home</Link>
                        </div>
                    }
                })()}

            </section>
        </>
    );
}

export default Projects;